import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'
import shop from '@/views/shop'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/shop',
      name: 'shop',
      component: shop
    }
  ]
})



